import React, {useEffect, useState} from 'react';
import { Space, Table, Tag, Radio, Button } from 'antd';

function Evaluation({data, title, score, setScore}) {

  const onChange = (e) => {setScore(prev => ({...prev, [e.target.rating]: e.target.value}));}

  data.forEach((x,i) => {
    const questionKey = `Q${i+1}`;
    x['rating1'] = questionKey;
    x['rating2'] = questionKey;
    x['rating3'] = questionKey;
    x['rating4'] = questionKey;
    x['rating5'] = questionKey;
  });

  const columns = [
    {
      title: title,
      dataIndex: 'question',
      key: 'question'
    },
    {
      title: "Strongly disagree",
      dataIndex: 'rating1',
      key: '1',
      align: 'center',
      render: (rating) => {
        const checked = Object.keys(score).includes(rating) === false ? false: score[rating] === 1 ? true: false;
        return (<Radio rating={rating} value={1} onChange={onChange} checked={checked}></Radio>)
      }
    },
    {
      title: "Disagree",
      dataIndex: 'rating2',
      key: '2',
      align: 'center',
      render: (rating) => {
        const checked = Object.keys(score).includes(rating) === false ? false: score[rating] === 2 ? true: false;
        return (<Radio rating={rating} value={2} onChange={onChange} checked={checked}></Radio>)
      }
    },
    {
      title: "Neither agree nor disagree",
      dataIndex: 'rating3',
      key: '3',
      align: 'center',
      render: (rating) => {
        const checked = Object.keys(score).includes(rating) === false ? false: score[rating] === 3 ? true: false;
        return (<Radio rating={rating} value={3} onChange={onChange} checked={checked}></Radio>)
      }
    },
    {
      title: "Agree",
      dataIndex: 'rating4',
      key: '4',
      align: 'center',
      render: (rating) => {
        const checked = Object.keys(score).includes(rating) === false ? false: score[rating] === 4 ? true: false;
        return (<Radio rating={rating} value={4} onChange={onChange} checked={checked}></Radio>)
      }
    },
    {
      title: "Strongly agree",
      dataIndex: 'rating5',
      key: '5',
      align: 'center',
      render: (rating) => {
        const checked = Object.keys(score).includes(rating) === false ? false: score[rating] === 5 ? true: false;
        return (<Radio rating={rating} value={5} onChange={onChange} checked={checked}></Radio>)
      }
    }
  ];

  return (
    <div>
      <Table columns={columns} dataSource={data} pagination={false} />
    </div>
  )
}
export default Evaluation;