import './App.css';
import { useState } from 'react';
import { Alert, Layout, theme, Steps, Button, Form} from 'antd';

import Evaluation from './evaluation/Evaluation';
import Demographic from './evaluation/Demographic';
import End from './evaluation/End';

//import usability from './assets/usability.json';
//import efficacy from './assets/efficacy.json';
//import utility from './assets/utility.json';
import question from './assets/questions.json';
import demographic from './assets/demographic.json';

const { Content } = Layout;


function App() {
  const [current, setCurrent] = useState(0);
  const [performanceScore, setPerformanceScore] = useState({});
  const [uiScore, setUiScore] = useState({});
  const [llmScore, setLlmScore] = useState({});
  const [usabilityScore, setUsabilityScore] = useState({});

  const [missingInput, setMissingInput] = useState(false);
  const [form] = Form.useForm();

  const steps = [
    {
      title: "System Performance",
      score: performanceScore,
      content: <><Evaluation data={question.performance} score={performanceScore} setScore={setPerformanceScore} /></>
    },
    {
      title: "Interactions & UI",
      score: uiScore,
      content: <><Evaluation data={question.ui} score={uiScore} setScore={setUiScore} /></>
    },
    {
      title: "LLM",
      score: llmScore,
      content: <><Evaluation data={question.llm} score={llmScore} setScore={setLlmScore} /></>
    },
    {
      title: "System Usability",
      score: usabilityScore,
      content: <><Evaluation data={question.usability} score={usabilityScore} setScore={setUsabilityScore} /></>
    },
    /*{
      title: "Demographic",
      content: <><Demographic form= {form} data={demographic}  /></>
    },*/
    {
      title: "End",
      content: <><End /></>
    },
  ];

  const items = steps.map((item) => ({
    key: item.title,
    title: item.title,
  }));

  const next = () => {
    console.log('performanceScore', performanceScore);
    const score = steps[current].score;
    const scoreLength = Object.keys(score).length;
    switch(current) {
      case 0:
        // usability
        if (scoreLength !== question.performance.length) {
          setMissingInput(true);
        } else {
          setCurrent(current + 1);
          setMissingInput(false);
        }
        break;
      case 1:
        // efficacy
        if (scoreLength !== question.ui.length) {
          setMissingInput(true);
        } else {
          setCurrent(current + 1);
          setMissingInput(false);
        }
        break;
      case 2:
        // utility
        if (scoreLength !== question.llm.length) {
          setMissingInput(true);
        } else {
          setCurrent(current + 1);
          setMissingInput(false);
        }
        break;
      case 3:
        // utility
        if (scoreLength !== question.usability.length) {
          setMissingInput(true);
        } else {
          setCurrent(current + 1);
          setMissingInput(false);
        }
        break;
      default:
        break;
    }
  };
  const prev = () => {
    setMissingInput(false);
    setCurrent(current - 1);
  };
  const onSubmit = () => {
    /*const fieldValue = form.getFieldValue();
    const completed = Object.keys(demographic).every(x => x in fieldValue);
    if (completed) {
      setCurrent(current + 1);
      setMissingInput(false);
    } else {
      setMissingInput(true);
    }*/
    setCurrent(current + 1);
    setMissingInput(false);
  };

  const {token: {colorBgContainer, borderRadiusLG}} = theme.useToken();

  return (
    <div className="App">
      <Layout>
        <Content style={{padding: '0 48px', minHeight: '100vh'}}>
          <div
            style={{
              background: colorBgContainer,
              minHeight: '100vh',
              width: 970,
              padding: 24,
              margin: 'auto',
              borderRadius: borderRadiusLG,
            }}
          >
            <Steps progressDot size='small' style={{paddingBottom: 10}} current={current} items={items} />
            <div>{steps[current].content}</div>
            <Alert
              style={missingInput ? {visibility: 'visible'} : {visibility: 'hidden'}}
              message="Please answer all the questions"
              banner
            />
            <div style={{marginTop: 24}}>
              {current > 0 && current < steps.length - 1 && (
                <Button style={{margin: '0 8px'}} onClick={() => prev()}>
                  Previous Section
                </Button>
              )}
              {current < steps.length - 2 && (
                <Button type="primary" onClick={() => next()}>
                  Next Section
                </Button>
              )}
              {current === steps.length - 2 && (
                <Button type="primary" onClick={onSubmit}>
                  Submit
                </Button>
              )}
            </div>
          </div>
        </Content>
      </Layout>
    </div>
  );
}

export default App;
